<template>
	<div id="root">
		<div class="navication" style="display:flex; flex-direction: row; height: 100px; background-color: #51BC02;">
			<div style="flex: 0.3; justify-content: center;align-items: center; ">
				<text style="color: #ffffff;"></text>
			</div>
			<div style="flex: 1;padding-top: 10px; justify-content: left;align-items:center;">
				<input type="text" auto-appear="false" placeholder="" class="input" value=""  @focus="toSearch"/>
			</div>
		    <div style="flex: 0.3; justify-content: center;align-items: center;">
				<text style="color: #ffffff;"></text>
				<text>{{msg}}</text>
	        </div>
		</div>
	</div>
</template>

<script>
var navigator = weex.requireModule('navigator');
module.exports = {
   data: function () {
     return {
       msg: "123"
     };
   },
   methods: {
      toSearch (){
      	 navigator.push({
          url: 'http://192.168.1.128:7777/dist/NavPage.js',
          animated: "true"
        })
      }
   }
  };
</script>

<style>
.input {
    font-size: 20px;
    height: 80px;
    width: 470px;
    background-color: #ffffff;
  }
</style>


